import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-category-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcCategoryAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

 
jcCategoryForm: FormGroup;
jccatId = 0;  
ajaxData : CategoryData[];
jcCategory= new CategoryData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.jccatId).subscribe(params=>{
			if(params.jccatId != undefined){
				this.jccatId=params.jccatId; 
			}
		}); 
	console.log("jccatId=="+this.jccatId);
	if(this.jccatId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.jcCategoryForm = new FormGroup({
			'jccatId' : new FormControl(''),
			'jccatName' : new FormControl('', [ Validators.required]),
			'jccatEntryId' : new FormControl(''),
			'jccatEntryDate' : new FormControl(''),
			'jccatModifiedId' : new FormControl(''),
			'jccatModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get jccatName(){ return this.jcCategoryForm.get('jccatName')};
	    get jccatEntryId(){ return this.jcCategoryForm.get('jccatEntryId')};
	    get jccatEntryDate(){ return this.jcCategoryForm.get('jccatEntryDate')};
	    get jccatModifiedId(){ return this.jcCategoryForm.get('jccatModifiedId')};
	    get jccatModifiedDate(){ return this.jcCategoryForm.get('jccatModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<CategoryData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jccategory-data ',
							 			JSON.stringify({ 'jccatId' :this.jccatId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.jcCategory = data['populateData'][0];
																		console.log("jccatModifiedId=="+this.jcCategory.jccatModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/jc-category-list']);      
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			console.log(this.bootstrapFromValues);
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.jcCategory[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.jcCategoryForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.jccatId == 0){ 
				this.jcCategory['requestType'] = 'add';
			}else{
				this.jcCategory['requestType'] = 'update';
				this.jcCategory['jccatId'] = this.jccatId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jccategory',
									JSON.stringify(this.jcCategory),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcCategoryForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcCategory);
	  }
 
  onDelete(){
		 this.jcCategory['requestType'] = 'delete';
		this.jcCategory['jccatId'] = this.jccatId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jccategory',
									JSON.stringify(this.jcCategory),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcCategoryForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class  CategoryData {
    jccatId: string;
	jccatName: string;
	jccatEntryId: string;
	jccatEntryBy: string;
	jccatEntryDate: string;
	jccatModifiedId: string;
	jccatModifiedBy: string;
	jccatModifiedDate: string;
  
}


